import React from "react";
import * as tf from '@tensorflow/tfjs';
import {
  AppBar,
  Toolbar,
  Grid,
  Container,
  Typography,
  Box,
  Button,
  Card,
  CardMedia,
  CardContent,
  CardActions,
  Fab,
} from "@material-ui/core";
import { ArrowUpward } from "@material-ui/icons";

const App = () => {
  const [img, setImg] = React.useState("gola.png");
  const [clase, setClase] = React.useState("Prediction...");
  const [model, setModel] = React.useState("");
  const imgRef = React.useRef();
  const classes = [<span style={{ color: "orange" }}>TIRADS4</span>, <span style={{ color: "red" }}>TIRADS5</span>, <span style={{ color: "#46BF71" }}>TIRADS1</span>, <span style={{ color: "#D7B723" }}>TIRADS3</span>, <span style={{ color: "#A9BF46" }}>TIRADS2</span>];

  let integrantes = [
    {
      title: "boris narvaez",
      image: "./boris.jpeg",
      nombre: "Boris M. Narvaez",
      bio:
        "Dr. Radiology Resident. Universidad Industrial de Santander / Universidad Autónoma de Bucaramanga",
    },
    {
      title: "federico lubinus",
      image: "./lubinus.jpeg",
      nombre: "Federico Lubinus",
      bio:
        "Dr. Radiology Specialist. Professor Universidad Autónoma de Bucaramanga.",
    },
    {
      title: "estefania arias",
      image: "./yhary.jpeg",
      nombre: "Yhary Arias",
      bio:
        "Systems Engineer, Universidad Autónoma de Bucaramanga.",
    },
    {
      title: "ariel ortiz",
      image: "./ariel.jpeg",
      nombre: "Ariel Ortiz",
      bio:
        "Systems Engineer, Universidad Nacional de Colombia, Master in UX.",
    },
  ];

  let tecnologias = [
    { "name": "TensorFlow", "img": "tensorflow.png" },
    { "name": "Keras", "img": "keras.png" },
    { "name": "Python", "img": "python.png" },
  ];
  function subirimg(f) {
    let reader = new FileReader();
    reader.readAsDataURL(f[0]);
    reader.onloadend = function (e) {
      setImg(reader.result);
      getClase().then(e => {
        let i = e.indexOf(Math.max(...e));
        setClase(classes[i]);
      });
    };
  }

  async function getClase() {
    let tensor = tf.browser.fromPixels(imgRef.current).resizeNearestNeighbor([128, 128]).toFloat().div(tf.scalar(255.0)).expandDims();
    return await model.predict(tensor).data();
  }

  React.useEffect(() => {
    async function getModel() {
      return await tf.loadGraphModel('./model.json');
    }
    getModel().then(e => setModel(e));
  }, []);

  return (
    <React.Fragment>
      <Container maxWidth="md" style={{ height: "100%" }} id="inicio">
        <Grid
          container
          spacing={2}
          justify="center"
          alignContent="center"
          alignItems="center"
        >
          <AppBar position="relative" color="transparent" elevation={0}>
            <Toolbar>
              <Box my={8}>
                <img src="./colibri.png" height="250" width="250" alt="colibri" />
              </Box>
              <Typography
                variant="h3"
                color="textSecondary"
                align="left"
                gutterBottom
                style={{ flexGrow: 1, fontWeight: "bold" }}
              >
                Viena Demo
              </Typography>
              <Button color="inherit" href="#infotec">TECHNICAL INFORMATION</Button>
              <Button color="inherit" href="#colab">colaborators</Button>
            </Toolbar>
          </AppBar>
          <Grid container item md={6}>
            <Typography
              variant="h2"
              color="textSecondary"
              align="left"
              gutterBottom
            >
              Description
            </Typography>
            <Typography
              variant="body1"
              color="textSecondary"
              align="right"
              gutterBottom
            >
              To design a model based on convoluted neural networks capable of performing automatic detection of thyroid nodule (TN) on ultrasound (US) and classifying its risk of malignancy based on EU TIRADS 2017 criteria. Also, to identify inter and intraobserver variability between a senior and junior radiologists for TN classification and compare levels of accuracy on classifying a TN between radiologists and a neural network model.
            </Typography>
          </Grid>
          <Grid
            container
            justify="center"
            alignItems="center"
            alignContent="center"
            item
            md={6}
            spacing={3}
          >
            <Grid item xs={6}>
              <input
                accept="image/*"
                style={{ display: "none" }}
                id="contained-button-file"
                type="file"
                onChange={(e) => subirimg(e.target.files)}
              />
              <label htmlFor="contained-button-file">
                <Button color="primary" variant="contained" component="span" style={{ borderTopRightRadius: 100, borderBottomRightRadius: 100 }}>
                  PREDICT
              </Button>
              </label>
            </Grid>
            <Grid item xs={6}>
              <Typography
                variant="h5"
                color="textSecondary"
                align="justify"
                gutterBottom
              >
                {clase}
              </Typography>
            </Grid>
            <Grid item
              xs={10}>
              <img src={img} alt="gola" height="300" width="300" ref={imgRef} style={{ border: "1px solid" }} />
            </Grid>
          </Grid>
        </Grid>
      </Container>
      <Container maxWidth="md" style={{ display: "block", height: "100%" }} id="infotec">
        <Grid
          container
          spacing={3}
          justify="center"
          alignContent="center"
          alignItems="center"
          style={{ paddingTop: 50 }}
        >
          <Grid item xs={12}>
            <Typography
              variant="h2"
              color="textSecondary"
              align="center"
              gutterBottom
            >
              TECHNICAL INFORMATION
            </Typography>
            <Typography
              variant="body1"
              color="textSecondary"
              align="justify"
              gutterBottom
            >
              The prediction model was built using a Convlutional Neural Network Architecture made in Python with Tensorflow and Keras. The model make a process to extract the most important characteristics from medical thyroid ultrasounds classifying them into few classes of TIRADS-EU.
            </Typography>

          </Grid>
          {tecnologias.map((tecnologia, index) =>
            <Grid key={index}
              item xs={4}>
              <Grid
                container
                justify="center"
                alignContent="center"
                alignItems="center"
              >
                <img src={tecnologia.img} height="100" width="100" alt={tecnologia.name} />
              </Grid>
              <Grid>
                <Typography gutterBottom variant="h6" align="center">
                  {tecnologia.name}
                </Typography>
              </Grid>
            </Grid>
          )}

        </Grid>
      </Container>
      <Container maxWidth="md" style={{ display: "block", height: "100%" }} id="colab">
        <Grid
          container
          spacing={2}
          justify="center"
          alignContent="center"
          alignItems="center"
        >
          <Grid
            container
            item
            md={12}
            justify="center"
            alignContent="center"
            alignItems="center"
          >
            <Typography
              variant="h2"
              color="textSecondary"
              align="left"
              gutterBottom
            >
              COLLABORATORS
            </Typography>
          </Grid>
          <Grid container spacing={2}>
            {integrantes.map((integrante, index) => (
              <Grid
                container
                justify="center"
                alignContent="center"
                alignItems="center"
                item
                md={3}
                key={index}
              >
                <CardMedia
                  style={{
                    height: 100,
                    width: 100,
                  }}
                  image={integrante.image}
                  title={integrante.title}
                />
                <Card>
                  <CardContent style={{ height: 300 }}>
                    <Typography gutterBottom variant="h6" align="center">
                      {integrante.nombre}
                    </Typography>
                    <Typography
                      variant="body2"
                      color="textSecondary"
                      align="center"
                    >
                      {integrante.bio}
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Button size="small" color="primary">
                      Learn More
                    </Button>
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Grid>
      </Container>
      <Fab color="primary" aria-label="add" style={{ position: "fixed", bottom: 50, right: 150 }} href="#inicio">
        <ArrowUpward />
      </Fab>
    </React.Fragment>
  );
};

export default App;